}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(49, 49, 49, 0.7);
}

.wrapper {
  position: relative;
  display: flex;
  overflow: hidden;
  flex: 1 1 auto;
  flex-direction: column;
  min-height: 100%;
  width: 100%;
}

.content {
  flex: 1 0 auto;
  margin: 0;
}

.header,
.footer {
  flex: 0 0 auto;
}

.img-wrap {
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: auto;
}

.img-wrap img,
.img-wrap picture,
.img-wrap svg {
  max-width: 100%;
  height: auto;
