}

.navbar {
  padding: 15px 0;
  background: #77eff1;
  transition: all 0.3s ease-in-out;
}

.navbar.fixed {
  position: fixed;
  z-index: 10;
  width: 100%;
  box-shadow: rgba(49, 49, 49, 0.2) 0 10px 10px -10px;
  transition: all 0.3s ease-in-out;
}

.logo {
  max-width: 100%;
}

.burger {
  position: relative;
  z-index: 10;
  display: none;
  margin: 0 0 0 15px;
  width: 36px;
  height: 25px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.burger span {
  position: absolute;
  top: 50%;
  display: block;
  width: 36px;
  height: 4px;
  border-radius: 2px;
  background: #313131;
  transition: all 0.3s ease-in-out;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}

.burger span::before {
  position: absolute;
  top: -9px;
  width: 36px;
  height: 4px;
  border-radius: 2px;
  background: #313131;
  content: "";
  transition: all 0.3s ease-in-out;
}

.burger span::after {
  position: absolute;
  top: 9px;
  width: 36px;
  height: 4px;
  border-radius: 2px;
  background: #313131;
  content: "";
  transition: all 0.3s ease-in-out;
}

.burger:hover {
  transition: all 0.3s ease-in-out;
}

.burger:hover span {
  background: #0f8b8d;
  transition: all 0.3s ease-in-out;
}

.burger:hover span::before {
  top: -12px;
  background: #0f8b8d;
  transition: all 0.3s ease-in-out;
}

.burger:hover span::after {
  top: 12px;
  background: #0f8b8d;
  transition: all 0.3s ease-in-out;
}

.burger.active {
  transition: all 0.3s ease-in-out;
}

.burger.active:hover {
  transition: all 0.3s ease-in-out;
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.burger.active span {
  background-color: transparent;
  transition: all 0.3s ease-in-out;
}

.burger.active span:before {
  top: 0;
  width: 36px;
  background-color: #0f8b8d;
  transition: all 0.3s ease-in-out;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.burger.active span:after {
  top: 0;
  width: 36px;
  background-color: #0f8b8d;
  transition: all 0.3s ease-in-out;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.menu {
  z-index: 2;
  display: flex;
  align-items: center;
  flex: 1 0 auto;
  flex-direction: row;
  justify-content: flex-end;
  gap: 5px;
}

.menu__item {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.menu__item-link {
  display: flex;
  align-items: center;
  padding: 0 15px;
  color: white;
  white-space: nowrap;
  transition: all 0.3s ease-in-out;
  gap: 5px;
}

.menu__item-link svg {
  width: 10px;
  height: 5px;
  transition: all 0.3s ease-in-out;
  fill: white;
}

.menu__item-link:hover {
  color: #0f8b8d;
  transition: all 0.3s ease-in-out;
}

.menu__item-link:hover svg {
  transition: all 0.3s ease-in-out;
  fill: #0f8b8d;
}

.menu__item-link.active {
  color: #0f8b8d;
}

.menu__item-link.active svg {
  transition: all 0.3s ease-in-out;
  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
  fill: #0f8b8d;
}

.submenu {
  position: absolute;
  top: 40px;
  left: 0;
  z-index: 1;
  display: none;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 0 0 4px 4px;
  background: #77eff1;
  transition: all 0.3s ease-in-out;
  -webkit-transform: translateY(-20%);
  transform: translateY(-20%);
}

.submenu.active {
  display: flex;
  transition: all 0.3s ease-in-out;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.submenu .menu__item {
  margin: 0;
  width: 100%;
}

.submenu .menu__item .menu__item-link {
  padding: 10px 15px 10px 10px;
  width: 100%;
}

.submenu .menu__item:not(:last-child) .menu__item-link {
  border-bottom: 1px dashed white;
}

