@media screen and (max-width: 1339.98px) {
  .container {
    max-width: 1100px;
  }
}

@media screen and (max-width: 991.98px) {
  .container {
    max-width: 970px;
  }

  .burger {
    display: block;
  }

  .menu {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    display: flex;
    visibility: hidden;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background: #313131;
    transition: all 0.4s ease-in-out;
    -webkit-animation: fade-out 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
    animation: fade-out 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
    pointer-events: all;
  }

  .menu__item {
    margin: 10px 0;
    opacity: 0;
    transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  .menu.active {
    visibility: visible;
    transition: all 0.3s ease-in-out;
    -webkit-animation: fade-in 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
    animation: fade-in 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  }

  .menu.active .menu__item {
    opacity: 1;
    transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  .menu.active .menu__item:nth-child(1) {
    transition-delay: 0.1s;
  }

  .menu.active .menu__item:nth-child(2) {
    transition-delay: 0.2s;
  }

  .menu.active .menu__item:nth-child(3) {
    transition-delay: 0.3s;
  }

  .menu.active .menu__item:nth-child(4) {
    transition-delay: 0.4s;
  }

  .menu.active .menu__item:nth-child(5) {
    transition-delay: 0.5s;
  }

  .menu.active .menu__item:nth-child(6) {
    transition-delay: 0.6s;
  }

  .menu.active .menu__item:nth-child(7) {
    transition-delay: 0.7s;
  }

  .menu.active .menu__item:nth-child(8) {
    transition-delay: 0.8s;
  }

  .menu.active .menu__item:nth-child(9) {
    transition-delay: 0.9s;
  }
}

@media screen and (max-width: 767.98px) {
  a[data-title]:hover::after {
    opacity: 0;
  }

  .container {
    max-width: 750px;
  }
}

@media screen and (max-width: 479.98px) {
  .container {
    max-width: none;
  }
}